input[type="text"] {
    display: block;
    width: 100%;
    height: 48px;
    padding: 0 15px;
    border: 2px solid teal;
    border-radius: 5px;
    font-size: 20px;

    @media screen and (max-width: 640px) {
      font-size: 16px;
  }

    &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: rgba(75, 151, 147, 0.541);
      }

}
