// Fonts
$hanalei_font: "Hanalei Fill", cursive;

.app {
    height: 100%;
    padding: 50px 0;
    @media screen and (max-width: 640px) {
        padding: 20px 0;
    }

    .container {
        max-width: 780px;
        margin: 0 auto;
        padding: 0 20px;
    }
}

.main_header {
    font-family: $hanalei_font;
    color: gray;
    opacity: 0.6;
    margin-bottom: 0;

    @media screen and (max-width: 640px) {
        font-size: 18px;
    }
}

.author {
    font-family: $hanalei_font;
    color: gray;
    opacity: 0.6;
    font-size: 12px;
    display: inline-block;
    margin-bottom: 15px;
}

.main_form {
    width: 100%;
    overflow: hidden;
}

.tasks {
    margin-top: 40px;
}

.todo_header {
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
}

.todo_title {
    color: teal;
    
    @media screen and (max-width: 640px) {
        font-size: 18px;
    }
}

.todo_title_empty {
    color: steelblue;
}

.info_panel {
    text-align: right;
}

